/* Reset some default styles */
html, body, ul, li, h1, h2, h3, p, button {
  margin: 0;
  padding: 0;
  list-style: none;
}

/* Set up global font and box-sizing */
body {
  font-family: Arial, sans-serif;
  box-sizing: border-box;
}

/* Global container */
.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

/* Header styles */
header {
  background-color: #333;
  color: #fff;
  padding: 10px 0;
}

nav ul {
  display: flex;
}

nav ul li {
  margin-right: 20px;
}

nav ul li a {
  color: #fff;
  text-decoration: none;
}

/* Main content styles */
main {
  padding: 20px 0;
}

section {
  margin-bottom: 40px;
}

h1, h2, h3 {
  margin-bottom: 10px;
}

/* Portfolio item styles */
.portfolio-item {
  margin-bottom: 20px;
}

.portfolio-item img {
  max-width: 100%;
}

/* Contact form styles */
form label {
  display: block;
  margin-bottom: 5px;
}

form input[type="text"],
form input[type="email"],
form textarea {
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

form button {
  padding: 10px 20px;
  background-color: #333;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

form button:hover {
  background-color: #555;
}

.carousel {
  display: flex;
  overflow-x: scroll;
  scroll-snap-type: x mandatory;
  -webkit-overflow-scrolling: touch; /* For smooth scrolling on iOS */
}

.carousel-item {
  flex: 0 0 auto;
  scroll-snap-align: start;
  margin-right: 20px; /* Adjust as needed */
}

.carousel-item img {
  width: 100%;
  height: auto}